* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #9757f0;
}

.container {
  height: fit-content;
  width: 700px;
  background-color: #fff;
  padding: 30px;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

h1 {
  text-align: center;
  padding: 0;
  margin: 0;
}
.center {
  text-align: center;
}
.listItems {
  width: 100%;
}
.info {
  display: inline-block;
  border: 1px solid #000000;
  font-size: 20px;
  font-weight: 500;
  line-height: 40px;
  text-align: center;
}
.name {
  width: 50%;
}
.age {
  width: 30%;
}
.remove {
  width: 20%;
  font-weight: 900;
  color: red;
}
.head {
  color: #ffffff;
  font-size: 25px;
  font-weight: 600;
  background-color: #6200ee;
}
.info-input {
  width: 40%;
  line-height: 20px;
  border: 1px solid #000000;
}
input[type="submit"] {
  width: 20%;
  display: inline-block;
  border: 1px solid #000000;
  background-color: #03dac6;
  font-size: 20px;
  font-weight: 500;
  line-height: 40px;
  text-align: center;
}
.alart {
  display: inline-block;
  text-align: center;
  color: #ffffff;
  font-size: 25px;
  font-weight: 700;
  width: 100%;
  height: 40px;
  background-color: #b00020;
}
.no-items {
  width: 100%;
}
